<template>
	<div class="bosom_one">
		<div class="top_nav">
			<div class="nav">
				<p class="title">云订单</p>
				<ul class="menuList" v-show="!isLog">
					<li ><a href="javascript:;" @click="login">登录</a></li>
					<li ><a href="javascript:;"  @click="login" >注册</a></li>
				</ul>
				<ul class="menuList avator" v-show="isLog">
					<li ><a href="javascript:;" ><img src="../assets/avator.png" alt="" /></a></li>
					<li ><a href="javascript:;" @click="login">注销</a></li>
				</ul>
			</div>
			<div class="header">
				<ul class="menuList">
					<li ><a href="javascript:;" @click="changeTab">切换</a></li>
					<li ><a href="javascript:;" >打印</a></li>
					<li ><a href="javascript:;" >下载</a></li>
					<li ><a href="javascript:;" >回传</a></li>
					<li ><a href="javascript:;" >关闭</a></li>
					<li ><a href="javascript:;" >查询</a></li>
				</ul>
			</div>
		</div>
		<!--二级路由-->
		<!--<router-view> </router-view>-->  
		
		<div class="bosom_side" >
			<!--v-show="isChange"-->
			<!--订单列表-->
			<div class="viscera" v-show="isChange">
				<!--搜索框-->
				<search></search>
				<!--表格orderFields
					load-on-start:false表示初始不加载表格数据
					
				-->
				<my-table 
					api-url="https://vuetable.ratiw.net/api/users"
      				:fields="orderFields"
      				:load-on-start="false" 
				></my-table>
				<!--采集按钮-->
				<button class="btn" @click="showModal">开始采集</button>
				
				<!--网站列表   模态框-->
				<modal-child :msg="myModal" @close="closeModal">
					<h3 slot="header">网站列表信息</h3>
					<div slot="body">
						<my-table 
							api-url="https://vuetable.ratiw.net/api/users"
							:fields="webListFields"
							:load-on-start="true"
						></my-table>
					</div>
					<div slot="footer">
						<button class="modal_btn" @click="closeModal">关闭</button><!--将myModal的值传给绑定的属性msg--><!--close监听子组件触发的事件-->
					</div>
				</modal-child>
			</div>
			
			<!--任务列表-->
			<!--v-show="!isChange"-->
			<!--api-url="https://vuetable.ratiw.net/api/users"-->
			<div class="viscera" v-show="!isChange" >
				
				<search></search>
				<div class="vuetable-wrapper">    
	<!--:api-mode="false" :loadOnStart = "false" :data="localData"
	      			:data-path="data"
	      			访问本地json文件的方法../static/LocalData.json-->
	      		<!--<div class="loader" v-if="loading">LOADING</div>-->
	      			<!--wrapper-class="vuetable-wrapper"
	      			@vuetable:loaded="onLoaded"
	      		-->
				<my-table 
					api-url="https://vuetable.ratiw.net/api/users"
	      			:fields="taskFields"
				></my-table>
				</div>	
			</div>
		</div>
		<my-footer class="footer"></my-footer>
	</div>
</template>

<script>
	import Vue from 'vue';
	import MyTable from './MyTable';
	import Search from './Search';
	import MyFooter from './MyFooter';
	import ModalChild from './ModalChild'; 
	import OrderFieldDefs from './OrderFieldDefs.js'; //fieldDefs
	import TaskFieldDefs from './TaskFieldDefs.js'; //fieldDefs
	import WebListFieldDefs from './WebListFieldDefs.js'; //fieldDefs
	//import LocalData from './LocalData.js'; //自定义的表格数据，暂时没有用到
	
	export default {
		name: 'Home',
		components: {
		    MyTable,
		    Search,
		    MyFooter,
		    ModalChild,
		},
		data() {
			return {
//				localData: LocalData,
				orderFields: OrderFieldDefs,
				taskFields: TaskFieldDefs,
				webListFields: WebListFieldDefs,
				isChange: true,
				isLog: false,//初始化登录状态为未登录
				myModal:false,
				loading: true,
			}
		},
//		props: ['title'],
		methods: {
			login() {
				this.isLog = !this.isLog;
				console.log(this.isLog);
				this.$router.push("user/login");
				
			},
			changeTab() {
				this.isChange = !this.isChange;
				console.log('2',this.isChange);
			},
			showModal() {
		        this.myModal = !this.myModal;
		    },
		    closeModal() {//定义close的事件函数
		        this.myModal = false;
		    },
		    onLoaded() {
            	this.loading = false;
		    }
					
		},
		created() {
				console.log('1', this.localData)
//				this.apiUrl = this.isChange == true ?  "" : "https://vuetable.ratiw.net/api/users";
//				this.fields = this.isChange == true ? this.orderFields : this.taskFields;
		},
		events: {
			'vuetable:loading': function() {
            // display your loading notification
               console.log ("load started");
        	},
        	'vuetable:load-success': function(response) {
            // hide loading notification
               console.log ("load completed");
        	},
		}
	}
	
</script>

<style scoped> 
	.bosom_one {
		height: 100%;
		box-sizing: border-box;
		padding-top: 100px;
		padding-bottom: 36px;
	}
	.bosom_side {
		height: 100%;
		padding: 12px 0;
	}
	.top_nav{
		width: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}
	.nav {
		height: 50px;
		background: url('../assets/HeadRightImg2.jpg') no-repeat right center;
		background-color: #fff;
		border-bottom: 1px solid rgba(122,122,122,0.125);
	}
	.nav .menuList {
		margin-top: 10px;
        margin-right: 20px;
	}

	.nav .menuList li {
		margin-left: 10px;
	}
	.header {
		background: #fff;
	    padding: 10px;
	    height: 48px;
	    box-sizing: border-box;
	    box-shadow: 0 2px 2px rgba(122,122,122,0.125);
	    position: relative;
		
	}
	.title {
		float: left;
		color: #d82b0a;
		height: 50px;
		line-height: 50px;
		font-size: 2em;
		font-weight: 500;
		margin-left: 40px;
	}
	.menuList {
		float: right;
	}
	.menuList li {
		list-style: none;
		float: left;
		background-color: #38a0f4;
		margin-right: 4px;
		padding:5px 10px ;
		font-size: 12px;
		border: 1px solid #38a0f4;
	}
	.menuList li:hover{
		background-color: #1690f3;
    	border-color: #1690f3;
	}
	.menuList li:nth-child(5){
		background-color: #fafafa;
		border: 1px solid #cdd6e1;
	}
	.menuList li a {
		text-decoration: none;
		color: #fff;
	}
	.menuList li:nth-child(5) a{
		color: #515151;
	}
	.menuList li:nth-child(5):hover{
		background-color: #e9e9e9;
    	border-color: #cdd6e1;
	}
	
	/*头像部分*/
		
	.nav .avator{
		margin-top: 2px;
	}
	.nav .avator li:nth-child(2){
		margin-top: 10px;
	}
	.nav .avator img{
		width: 32px;
    	height: 32px;
    	vertical-align: middle;
    	border-radius: 16px;
	}
	
	.nav .avator li:nth-child(1) {
		background: none;
		border:0 none;
		margin-top: 3px;
	}
	
	/*头像部分结束*/
	
	.bosom_side .viscera {
		margin: 10px 18px;
	     padding: 0 20px;
	    background: #fff;
	    border-top: 3px solid #e7eaec;
	    height: 100%;
	    overflow-y: auto;
	    position: relative;
	}
	
	.btn{
		padding: 10px 20px;
		position: absolute;
		top: 60%;
		left: 50%;
		transform: translate(0, -50%);
		background-color: #38a0f4;
		border: 1px solid #38a0f4;
		font-size: 16px;
		color: #fafafa;
	}
	
	.footer {
		position: absolute;
		bottom: 0;
		left:0;
	}
	
	/*模态框样式*/
	.modal_btn {
		background-color: #38a0f4;
		border: 1px solid #38a0f4;
		color: #fff;
	}
	
	/*loading style*/
	/* Loading Animation: */
.vuetable-wrapper {
    position: relative;
    opacity: 1;
}
.loader {
    /*visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s linear;*/
    background: url('../assets/loading.gif') no-repeat bottom center;
    width: 200px;
    height: 30px;
    font-size: 1em;
    text-align: center;
    margin-left: -100px;
    letter-spacing: 4px;
    color: #ccc;
    position: absolute;
    top: 160px;
    left: 50%;
}
.loading .loader {
    visibility: visible;
    opacity: 1;
    z-index: 100;
}
.loading .vuetable{
    opacity:0.3;
    filter: alpha(opacity=30); /* IE8 and earlier */
}
</style>


